/*
 *  Collection banner
 *    Styles for the Collection banner  section on the home page.
 *
 *
 *  Version:
 *    1.0.0 - 2020/07/14
 */

@import '../base';

.c-collection-products {
  @include t-gutters;
  margin-left: t-to-rem(20);
  margin-right: t-to-rem(20);

  &__fixed-btn--wrap {
    display: flex;
    position: fixed;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
  }
  &__fixed-btn {
    border-radius: 10rem;
    &::before {
      opacity: 0;
    }
  }
  &__accordion-wrapper {
    min-width: 250px;
    width: 250px;
  }
  &__accordion {
    &:first-of-type {
      border-top: 1px solid rgba($s-color--background-secondary, .1);
    }

    border-bottom: 1px solid rgba($s-color--background-secondary, .1);

    &__title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;
      padding: t-to-rem(18) 0;
      color: $s-color--font-1;

      .o-icon {
        pointer-events: none;
      }
    }

    &__content {
      max-height: 0;
      overflow: hidden;
      @include t-anim--transition(max-height margin, $s-anim--time-med);
      color: $s-color--font-1;

      .is-expanded & {
        max-height: 30rem;
        margin-bottom: t-to-rem(24);
        overflow: auto;
      }
    }

    .o-icon {
      @include t-width-and-height(12);

      &--minus {
        display: none;
      }
    }

    &.is-expanded {
      .o-icon {
        &--minus {
          display: block;
        }

        &--plus {
          display: none;
        }
      }
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 2.5rem 0;
    position: relative;

    &:first-child {
      position: relative;

      &::after {
        content: '';
        height: 1px;
        width: 100vw;
        position: absolute;
        bottom: 0;
        left: -2.5rem;
        background-color: $s-color--background-septenary;
        z-index: 1;
      }
    }

    &:last-child {
      padding-bottom: .5rem;
    }

    &-content {
      width: 60%;
    }

    afterpay-placement {
      position: relative;
      width: 250px;
      text-align: center;
      z-index: 4;
    }
  }

  &__title-wrapper {
    display: block;
    position: relative;
    z-index: 1;
    padding: t-to-rem(20) 0;

    &::after {
      content: '';
      height: 100%;
      width: 105vw;
      position: absolute;
      top: 0;
      left: -2.5rem;
      background-color: $s-color--background-septenary;
      z-index: -1;
    }
  }

  &__title {
    margin: t-to-rem(7) 0 t-to-rem(18);
    font-weight: 700;
    color: $s-color--font-1;
  }

  &__subtitle {
    text-transform: uppercase;
    color: $s-color--grey-green;
    margin: 0;
    font-size: 12px;
    min-height: t-to-rem(26);
  }

  &__text {
    margin: 1rem 0;
    text-align: left;
  }

  .oke-sr-rating {
    display: inline;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 500;
    top: -1px;
    position: relative;
  }

  .okeReviews,
  .oke-sr-stars {
    line-height: 1;
  }

  .oke-sr-count-number {
    position: relative;
    font-size: 15px;
    line-height: 1.2;
    font-weight: 500;
    color: $s-color--grey-green;
    top: -1px;
    position: relative;

    &:before {
      content: "(";
      display: inline;
      position: relative;
    }

    &:after {
      content: ")";
      display: inline;
      position: relative;
    }
  }

  .oke-sr-label-text {
    display: none;
  }

  &__image-container {
    width: 40%;
    z-index: 3;
    position: relative;
    align-self: flex-start;
    padding-top: t-to-rem(20px);
    padding-top: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;

    position: absolute;
    right: 0;
    top: 0;
    padding-top: t-to-rem(40);

    img {
      max-height: t-to-rem(286);
      min-height: t-to-rem(230);
      height: auto;
      width: auto;
      object-fit: contain;
    }
  }

  &__btn-wrapper {
    display: flex;
    align-items: center;
    margin-top: t-to-rem(20);
    position: relative;
    width: 100%;
    max-width: t-to-rem(400);
    padding-right: 10px;


    .o-btn {
      padding-left: t-to-rem(4);
      padding-right: t-to-rem(4);
      font-size: t-to-rem(16);
      line-height: 1.3;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-grow: 1;
      min-height: t-to-rem(44);

      &.is-primary {
        flex-grow: 3;
      }
    }
  }

  &__btn {
    @include t-font--type-5;
  }

  &__btn--show-product {
    margin-right: 20px;
  }

  &__add-cart  {
    margin-top: 0;
    @include t-font--type-2;
    // width: 250px;
    width: auto;

    .is-divider {
      margin: 0 t-to-rem(7);
    }

    &-price {
      font-size: inherit;
      display: inline;
      color: $s-color--font-6;
      line-height: inherit;

      &-compare {
        text-decoration: line-through;
        color: rgba($s-color--font-6, .5);
        margin-right: t-to-rem(8);
      }
    }
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-xsmall-up) {
    &__btn-wrapper {
      padding-right: 0;
    }
  }

  @media screen and (min-width: 414px) {
    margin-left: t-to-rem(40);
    margin-right: t-to-rem(40);

    &__title-wrapper {
      padding: 1.75rem 0;
    }
    
    &__image-container {
      padding-top: t-to-rem(40);
    }
  }

  @include t-media-query($s-small-up) {
    display: flex;
    margin-left: t-to-rem(25);
    margin-right: t-to-rem(25);

    &__accordion {
      &__title {
        padding: t-to-rem(20) 0;
      }

      &__content {
        .is-expanded & {
          max-height: 30rem;
        }

        .o-icon {
          @include t-width-and-height(14);
        }
      }
    }

    &__btn-wrapper {
      padding-left: t-to-rem(25);
    }

    &__item {
      width: 50%;

      &:first-child {
        border-right: 1px solid $s-color--background-septenary;
        padding-right: t-to-rem(25);

        &::after {
          height: 0;
        }
      }

      &:last-child {
        padding-left: t-to-rem(25);
        padding-bottom: t-to-rem(40);
      }

      &-content {
        padding-left: t-to-rem(25);
        width: 65%;
        flex-grow: 1;
      }
    }

    &__title-wrapper {
      padding: t-to-rem(25) 0;

      &::after {
        content: '';
        width: 156%;
        left: t-to-rem(-25)
      }
    }

    &__image-container {
      width: 35%;
    }

    &__image {
      // width: 130%;
    }

  }

  @include t-media-query($s-medium-up) {
    margin-top: 5rem;
    margin-bottom: 5rem;
    margin-left: t-to-rem(50);
    margin-right: t-to-rem(50);

    &__item {


      &:first-child {
        .c-collection-products__image-container {
          margin-right: t-to-rem(50);
        }
      }
      &:last-child {
        padding-left: t-to-rem(50);
      }

      &-content {
        padding-left: t-to-rem(50);
      }

      afterpay-placement {
        position: relative;
        min-width: 25.625rem;
      }
    }

    &__accordion-wrapper {
      min-width: t-to-rem(410);
      width: 95%;
    }

    &__btn-wrapper {
      max-width: t-to-rem(500);
      padding-left: t-to-rem(50);

      .o-btn {
        min-height: t-to-rem(66);
        font-weight: 700;
        font-size: t-to-rem(20);
        line-height: 1.2;
      }
    }

    &__btn--show-product {
      margin-right: 30px;
    }

    &__title-wrapper {
      padding: t-to-rem(50) 0;

      &::after {
        width: 160%;
        left: t-to-rem(-50);
      }
    }

    &__subtitle {
      font-size: t-to-rem(16);
    }

    .oke-sr-count-number,
    .oke-sr-rating {
      font-size: 22px;
    }

    &__image-container {
      padding-top: t-to-rem(60);

      img {
        max-height: t-to-rem(400);
        min-height: unset;
        height: auto;
        width: 100%;
        object-fit: contain;
      }
    }

    &__image {
      width: 100%;
    }

    &__add-cart  {
      .is-divider {
        margin: 0 t-to-rem(12);
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    margin-left: 7%;
    margin-right: 7%;

    &__item {
      &:first-child {
        padding-right: 6rem;
      }

      &:last-child {
        padding-left: 6rem;
      }

      &-content {
        width: 60%;
      }
    }

    &__title-wrapper {
      padding: t-to-rem(60) 0;
    }

    &__subtitle {
      font-size: t-to-rem(20);
    }

    &__image-container {
      width: 40%;
      padding-top: t-to-rem(60);
    }

    &__btn-wrapper {
      margin-top: t-to-rem(30);
    }

    &__btn-wrapper {
      .o-btn {
        font-size: t-to-rem(25);
      }
    }
  }
}
